<template>
  <section class="maxBox">
    <div class="wa_top">
      <span></span>
      <div class="number">共抓到<em v-text="userInfo.num"></em>个</div>
      <img :src="userInfo.head" alt="" class="head">
      <!-- 头像 -->
      <p class="name"><em>{{userInfo.name}}</em><span class="id">(ID：<em>{{userInfo.uid}}</em>)</span></p>
      <p class="code_i" v-if="inviteCode>0"><em>邀请码：</em>{{inviteCode}}</p>
    </div>

    <img src="http://lianai-image-sys.qiniudn.com/wa/btn.png" alt="" class="down" @click="jumpDownload">
    <ul class="waLists ks-clear">
      <li v-for="doll in dollList">
        <div><img :src="doll.path" alt=""></div>
        <p class="pic_name" v-text="doll.name"></p>
        <p class="pic_time" v-bind:name="doll.getTime | fmtDate(doll.getTime)">{{doll.getTime | fmtDate(doll.getTime)}}</p>
      </li>
    </ul>
    <div class="footer r_footer"><img :src="imgChange?'http://lianai-image-sys.qiniudn.com/wa/bg_foot_1.png':'http://lianai-image-sys.qiniudn.com/wa/bg_foot_2.png'" alt="" @click="jumpDownload"></div>
  </section>
</template>
<script>
import user from './assets/js/user.js'
import doll from './api/doll/doll'
import common from './common/common.js'
import httpUtils from './common/httpUtils.js'

export default {
  name: 'app',
  components: {},
  data() {
    return {
      uid: 1832306,
      userInfo: {},
      dollList: [],
      imgChange:true,
      inviteCode:0,
    }
  },
  filters: {
    fmtDate: function(value) {
      if (!value) return ''
      return common.fmtDate(value);
    }
  },
  created: function() {
    this.uid=httpUtils.request("uid");
    console.log(this.uid);
    let self = this;
    doll.getDolls(this.uid).then(function(res) {
      console.log(res);
      self.userInfo = res.userInfo;
      self.dollList = res.dollList;
      if (res.inviteCode) {
        self.inviteCode=res.inviteCode;
      }
    }).catch(function(err) {
      console.log(err);
    });
    setInterval(() => { 
            this.changeImg();
        }, 500)
  },
  methods: {
    jumpDownload: function() {
      httpUtils.jumpDownloadUrl();
    },
    changeImg: function() {
      this.imgChange=!this.imgChange;
    }
  }
}

</script>
<style>

.wa_top{
    width:100%;
    height:6.46rem;
    background: url("http://lianai-image-sys.qiniudn.com/wa/bg.png") no-repeat center center;
    -webkit-background-size: 87% 100%;
    background-size: 87% 100%;
    position: relative;
    padding-top: 2.08rem;
    box-sizing: border-box;
    text-align: center;
    }
    .name{
    color:#222;
    font-size: 0.32rem;
    margin-top:0.2rem;
    }
    .id{
    font-size: 0.2rem;
    color:#666;
    opacity: 0.5;
    }
    .wa_top>span{
    position: absolute;
    top:0;
    left:0;
    width:100%;
    height:6.46rem;
    background: url("http://lianai-image-sys.qiniudn.com/wa/s_top.png") no-repeat center center;
    -webkit-background-size: 100% 100%;
    background-size: 100% 100%;
    }
    .number{
    position: absolute;
    bottom:0.3rem;
    left:0;
    text-align: center;
    width:100%;
    height:auto;
    font-size: 0.34rem;
    }
    .number>em{
    font-size: 0.58rem;
    color:#fff;
    margin:0 0.05rem;
    text-shadow: 0 1px #000, 1px 0 #000, -1px 0 #000, 0 -1px #000;
    }
    .down{
    width:3.8rem;
    height:0.56rem;
    margin:0.32rem auto 0.56rem;
    }
    .head{
    width:2rem;
    height:2rem;
    border-radius: 50%;
    margin:0 auto;
    }
    .waLists{
    width:100%;
    height:auto;
    padding:0 0.2rem;
    box-sizing: border-box;
    margin-bottom: 2.58rem;
   /* background: #fff203;*/
    }
    .waLists>li{
    width:49%;
    float: left;
    height:4.58rem;
    border-radius: 0.16rem;
    background: #fff;
    margin-top:0.14rem;
    }
    .waLists>li:nth-of-type(even){
    float: right;
    }
    .waLists>li>div{
    width:100%;
    height:3.46rem;
    text-align:center;
    }
    .waLists>li>div>img{
    width:100%;
    height:100%;
    border-top-left-radius: 0.16rem;
    border-top-right-radius: 0.16rem;
    }
    .pic_name{
    margin-top:0.24rem;
    }
    .pic_name,.pic_time{
    font-size: 0.28rem;
    color: #333;
    padding:0 0.16rem;
    box-sizing: border-box;
    font-weight: 700;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
    }
    .pic_time{
    font-size: 0.24rem;
    color:#999;
    font-weight:normal;
    }
    .r_footer{
    position: fixed;
    bottom:0;
    }
    .code_i{
    margin-top:0.1rem;
    color:#000;
    }
</style>



